import Vue from 'vue'
import Pptx from './pptx.vue'

/**
 * pptxvuepptxjs
 * @param buffer 
 * @param target 
 */
export default async function renderPptx(buffer, target) {
  return new Vue({
    render: (h) => h(Pptx, { props: { data: buffer } }),
  }).$mount(target)
}
